$lightGreen : #08f0f0;
.app{
    display: flex;
    justify-content: center;
    .container{
    width: 1024px;
        .link{
            text-decoration: none;
            color: inherit;
        }
        .auth{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 100vh;
            background-color: $lightGreen;

            h1{
                font-size: 20px;
                color:  teal;
                margin-bottom: 20px;
            }
            form{
                display: flex;
                flex-direction: column;
                padding: 50px;
                background-color: white;
                width: 200px;
                gap: 20px;

                input{
                    padding: 10px;  
                    border: none;
                    border-bottom:1px solid gray ;
                }
                butoon{
                        padding: 10px;
                        border: none;
                        background-color: teal;
                        cursor: pointer;
                        color: white;
                }
                p{
                    font-size: 12px;
                    color: red;
                    text-align: center;
                }
                span{
                    font-size: 12px;
                    text-align:center;
                }
            }
        }
        .navbar{
            .container{
                padding: 10px 0px;
                display: flex;
                justify-content: space-between;
                .logo{
                    img{
                        width: 120px;
                    }
                }
                .links{
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    h6{
                        font-size: 16px;
                        font-weight: 300;
                    }
                    span{
                        cursor: pointer;
                    }
                    .phanhoi{
                        background: $lightGreen;
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        display: flex;
                        align-items: center;
                        justify-self: center;
                        font-weight: 300;
                        border: 1px solid white;
                        &:hover{
                            width: 48px;
                            height: 48px;
                            color: teal;
                            background-color: white;
                            border: 1px solid teal;
                        }
                    }
                }
            }
        }
        footer{
            margin-top: 100px;
            padding: 20px;
            background-color: $lightGreen;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 12px;
            img{
                height: 50px;
            }
        }
        .home{
            .posts{
                margin-top: 50px;
                display: flex;
                flex-direction: column;
                gap: 150px;

                .post{
                    display: flex;
                    gap: 100px;

                    &:nth-child(2n+1){
                        flex-direction: row-reverse;
                    }
                    .img{
                        flex: 2;
                        position: relative;
                        &::after{
                            content: "";
                            width: 100%;
                            height: 100%;
                            background-color: $lightGreen;
                            position: absolute;
                            top: 20px;
                            left: -20px;
                            z-index: -1;

                        }
                        img{
                            width: 100%;
                            max-height: 400px;
                            object-fit: cover;
                        }
                    }
                    .content{
                        flex: 3;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        h1{
                            font-size: 48px;
                        }

                        p{
                            font-size: 18px;
                        }

                        button{
                            width: max-content;
                            padding: 10px 20px;
                            border: none;
                            cursor: pointer;
                            background-color: white;
                            border: 1px solid teal;
                            color: teal;
                            &:hover{
                                border: 1px solid white;
                                background-color:$lightGreen;
                                color: black;
                            }
                        }
                    }
                }
            }
        }
        .feedback{
            display: flex;
            gap: 50px;
            .content{
                flex: 5;
                display: flex;
                flex-direction: column;
                gap: 30px;
                img{
                    width: 100%;
                    height: 300px;
                    object-fit: cover;

                }
                .user{
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    font-size: 14px;
                    img{
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        object-fit: cover;

                    }
                    span{
                        font-weight: bold;
                    }
                }
                .edit{
                    display: flex;
                    gap: 5px;
                    img{
                        width: 20px;
                        height: 20px;
                        cursor: pointer;
                     }
                }
                h1{
                    font-size: 42px;
                    color: #333;
                }
                p{
                    text-align: justify;
                    line-height: 30px;
                }
            }
            .menu{
                flex: 2;
                display: flex;
                flex-direction: column;
                gap: 25px;
                h1{
                    font-size: 20px;
                    color: #555;
                }
                .post{
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                    img{
                        width: 100%;
                        height: 200px;
                        object-fit: cover;
                    }
                    h2{
                        color: #555;
                    }
                    button{
                        width: max-content;
                        padding: 7.5px 15px;
                        border: none;
                        cursor: pointer;
                        background-color: white;
                        border: 1px solid teal;
                        color: teal;
                        &:hover{
                            border: 1px solid white;
                            background-color:$lightGreen;
                            color: black;
                        }}
                }
            }
        }
    }
}